body {
    margin:0;
    padding: 0;
    box-sizing: border-box;
    background-color: #fff;
    font-family: 'Inter', sans-serif;
    color: #fff;
}

*{
    box-sizing: border-box
}

header{
    display: flex;
    padding: 10px;
    align-items: center;
    justify-content: center;
    background: linear-gradient(90deg,  #802241 1.18%, #b626d3 100%);
}

header img {
    margin-left: auto;
}

header h3 {
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
}

.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 3em;
}

.form-container {
    /* text-align: center; */
    display: grid;
    grid-template: 50px 50px / 1fr 1fr;
    align-content: center;
    margin-bottom: 30px;
    max-width: 100%;
    gap: 10px;
}

.form-container input {
    font-family: 'Inter', sans-serif;
    text-indent: 10px;
    border-radius: 8px;
    font-weight: bold;
    font-size: 1.3em;
    outline: none;
    border: 1px solid purple;
}

.form-container input:focus {
    border-width: 3px;
}

.form-container button {
    grid-column: 1/-1;
    background: linear-gradient(90.41deg, #8d1f6a 1.14%, #da1818 100%);
    border: none;
    color: #fff;
    font-family: 'Inter', sans-serif;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bold;
    font-size: 1.2em;
}

.form-container button:active{ 
   /* margin: 0 auto;
   width: 98%;
   height: 98%; */
   border-left: 5px solid transparent;
   border-right: 5px solid transparent;
   border-top: 2px solid transparent;
   border-bottom: 2px solid transparent;
   font-size: 1.1em;
}


.img-container{
    max-width: 400px;
    margin: 0 auto;
    position: relative;
}

.img-container img {
    width: 100%;
}

.top-text, .bottom-text {
    position: absolute;
    font-size: 1.7em;
    color: white;
    /* background:linear-gradient(90deg, wheat 1.9%, rgb(248, 236, 236) 100%); */
    border-radius: 1px;
    padding: 8px 18px;
    font-weight: 600;
    left: 50%;
    text-shadow:
    2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}

.top-text  {
    top: 60px;
}

.bottom-text {
    bottom: 60px;
}

h6 {
    color: black;
}